.centered {
    display: flex;
    align-items: center;
    justify-content: center;
}

.card-elements-container {
    border-radius: 20px;
    -webkit-box-shadow: 0 0 6px 6px rgba(0, 0, 0, 0.05);
    box-shadow: 0 0 6px 6px rgba(0, 0, 0, 0.05);
    margin-left: 18px;
    margin-right: 18px;
    width: calc(100% - 32px);
    padding-top: 8px;
    padding-bottom: 8px;
}

.card-elements-container .cell {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -ms-flex-pack: justify;
    justify-content: space-between;
    margin: 8px;
}

.card-elements-container .left {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
}

.card-elements-container .text {
    height: 22px;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.29;
    letter-spacing: -0.4px;
    color: #76787a;
    margin-left: 8px;
}

.card-button .icon {
    margin-right: 8px;
}